import React ,{useEffect, useState}from "react";
import {makeStyles} from "@material-ui/core/styles";
import Autocomplete from '@material-ui/lab/Autocomplete'
import TextField from '@material-ui/core/TextField';
import axios from 'axios';


const CalendarSearchField = () => {
  const classes = useStyles();
  const options = filling.map((option) => {
    const firstLetter = option.title[0].toUpperCase();
    return {
      firstLetter: /[0-9]/.test(firstLetter) ? '0-9' : firstLetter,
      ...option,
    };
  });
  return (
      <>
        <Autocomplete
            options={options.sort((a, b) => -b.firstLetter.localeCompare(a.firstLetter))}
            groupBy={(option) => option.firstLetter}
            getOptionLabel={(option) => option.title}
            blurOnSelect = 'touch'
            clearOnBlur
            style={{ width: 300 }}
            renderInput={(params) => (
                <TextField {...params} label="поиск по имени" margin="normal" variant="outlined" size='small'
                />

            )}
        />

      </>
  )
}


const filling = [
  { title: 'Маруся', date: '28.02.20' },
  { title: 'Настя', date: '28.02.20' },
  { title: 'Валя', date: '28.02.20' },
  { title: 'Маруся1', date: '28.02.20' },
  { title: 'Настя1', date: '28.02.20' },
  { title: 'Валя', date: '28.02.20' },];

const useStyles = makeStyles((theme) => ({
  SummaryRightPanelSearchLine: {
    position: 'relative',
    flexBasis: '50%',
    [theme.breakpoints.down('xs')]: {
      display: 'none'
    },
  },
  searchIconMobile: {
    display: 'none',
    fill: '#DADADA',
    [theme.breakpoints.down('xs')]: {
      display: 'block',
      marginRight: '2px'
    },
  },
  searchField: {
    backgroundColor: 'rgba(218, 218, 218, 0.5)',
    border: 'none',
    height: '29px',
    margin: '10px 0',
    paddingLeft: '40px',
    width: '100%',
    maxWidth: '300px',
    '&:focus': {
      outline: 'none'
    },
  },

  searchIcon: {
    fill: '#fff',
    position: 'absolute',
    top: '13px',
    left: '10px'
  }
}))

export default CalendarSearchField;
