import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {makeStyles} from '@material-ui/core/styles';
import axios from 'axios';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextFieldLite from '../../../common/TextFieldLite';
import {editIngredients} from '../../../../actions/recipes/makeRecipeActions';

const SearchField = ({getDataUrl, label, noOptionsText, width, elem}) => {
  const dispatch = useDispatch();
  const [ingredients, setIngredients] = useState([]);
  const [selectedIngredient, setSelectedIngredient] = useState(null);

  useEffect(() => {
    getCategories();
  }, []);

  const getCategories = () => {
    console.log('getCategories');
    try {
      axios.get(getDataUrl,
      ).then(data => {
        setIngredients(data.data);
      });
    } catch (e) {
      console.error(e);
    }
  };

  const changeIngredient = (event, newValue) => {
    setSelectedIngredient(newValue);
    if(newValue) {
      dispatch(editIngredients({
        ...elem,
        name: newValue.title,
        price: newValue.price_per_gram,
      }));
    }
  };

  return (
      <>
        <Autocomplete
            options={ingredients}
            getOptionLabel={(option) => option.title}
            noOptionsText={noOptionsText}
            value={selectedIngredient}
            onChange={changeIngredient}
            style={{width: width}}
            renderInput={(params) => (
                <TextFieldLite
                    {...params}
                    label={label}
                    margin="normal"
                    variant="outlined"
                    size='small'
                    onChange={changeIngredient}
                />
            )}
        />
      </>
  );
};

