import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import OrderImage from './OderImage';
import basket from '../../../images/bаsket.svg';
import styles from '../../../css/modules/drawer.module.css';
import clock from '../../../images/clock.svg';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import DownArrow from '../../../components/common/DownArrow';

const MakeOrderMakeOrderCreated = ({isOpen}) => {
  const classes = useStyles();
  const [ready, setReady] = useState(false);
  const [collected, setCollected] = useState(false);
  const [baked, setBaked] = useState(false);
  const [countSize, setCountSize] = useState(false);
  const [myCakes, setMyCakes] = useState(false);

  const readyHandler = (event) => {
    setReady(event.target.checked);
  };

  const collectedHandler = (event) => {
    setCollected(event.target.checked);
  };

  const bakedHandler = (event) => {
    setBaked(event.target.checked);
  };

  const countSizeHandler = (event) => {
    setCountSize(event.target.checked);
  };

  const myCakesHandler = (event) => {
    setMyCakes(event.target.checked);
  };

  return (
      <div className={classes.makeOrderCreatedBlock}>
        <div className={classes.makeOrderCreatedBlockLineOne}>
          <OrderImage width='250px' height='250px'/>
          <div className={classes.makeOrderCreatedBlockLineOneFirst}>
            <div className={classes.makeOrderCreatedBlockLineOneBasket}>
              <img src={basket} className={classes.basketIcon}/>
            </div>
            <div className={classes.makeOrderCreatedBlockLineOneFirstRight}>
              <p className={classes.makeOrderCreatedBlockLineOneTextPreview}>
                Здесь будет Небольшое Описание
                Позиции в общих Чертах Важные Моменты
                Цветовая Гамма Как Подписать и Т.Д
              </p>
              <div className={classes.makeOrderCreatedBlockLineOneTextMore}>Весь Текст</div>
            </div>
            <div className={classes.makeOrderCreatedBlockLineOneSecond}>
              <div className={classes.makeOrderCreatedBlockLineOneSecondName}>
                <div className={classes.makeOrderCreatedBlockLineOneProductNameLine}>
                  Сливочный
                </div>
                <div className={classes.makeOrderCreatedBlockLineOneProductNameLineInfo}>
                  2кг &#x25CF; 2яруса &#x25CF; мастика
                </div>
              </div>
              <div className={classes.makeOrderCreatedBlockLineOneSecondForm}>
                  <div className={classes.makeOrderCreatedBlockLineOneSecondCircle}>
                  </div>
              </div>
              <div className={classes.makeOrderCreatedBlockLineOneSecondData}>
                <div className={classes.makeOrderCreatedBlockLineOneSecondDataAll}>3000</div>
                <div className={classes.makeOrderCreatedBlockLineOneSecondDataPerKg}>(1500/кг)</div>
                <div className={classes.makeOrderCreatedBlockLineOneSecondDataRest}>
                  <span>Остаток</span>
                  <span className={classes.dataRestNumber}>2000</span></div>
                <div className={classes.makeOrderCreatedBlockLineOneSecondDataIncome}>
                  <span>Заработок</span>
                  <span className={classes.dataIncomeNumber}>2000</span></div>
                <div className={classes.makeOrderCreatedBlockLineOneSecondDataIncomeSup}>(Ф800р)</div>
              </div>
            </div>
          </div>
        </div>
        <div className={classes.makeOrderCreatedBlockLineTwo}>
          <img src={clock} className={styles.iconClock}/>
          <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                    checked={myCakes}
                    onChange={myCakesHandler}
                    name="myCakes"
                />
              }
              label="Мои торты"
          />
          <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                    checked={countSize}
                    onChange={countSizeHandler}
                    name="countSize"
                />
              }
              label="Посчитан(размеры)"
          />
          <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                    checked={baked}
                    onChange={bakedHandler}
                    name="baked"
                />
              }
              label="Выпечен"
          />
          <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                    checked={collected}
                    onChange={collectedHandler}
                    name="collected"
                />
              }
              label="Собран"
          />
          <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                    checked={ready}
                    onChange={readyHandler}
                    name="ready"
                />
              }
              label="Готов"
          />
        </div>

        <div className={classes.makeOrderCreatedBlockLineThree}>
          <div className={classes.makeOrderCreatedBlockLineThreeLeft}>
            <div className={classes.makeOrderCreatedBlockLineThreeWeightBare}>
              <span>Вес Голого</span>
              <span className={classes.dataNumber}>2000</span></div>
            <div className={classes.makeOrderCreatedBlockLineThreeWeightCloth}>
              <span>Вес Обмаз</span>
              <span className={classes.dataNumber}>2000</span>
            </div>
            <div className={classes.makeOrderCreatedBlockLineThreeWeightCover}>
              <span>Вес Покрытия</span>
              <span className={classes.dataNumber}>2000</span>
            </div>
          </div>
          <div className={classes.makeOrderCreatedBlockLineThreeRight}>
            <div className={classes.makeOrderCreatedBlockLineThreeWeightCoverRight}>
              <span>Вес Покрытия</span>
              <span className={classes.dataNumberCoverRight}>2000</span></div>
          </div>
        </div>
        <div className={classes.arrowBlock}>
          <DownArrow isOpen={isOpen}/>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeOrderCreatedBlock: {
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '3%',
    flexWrap: 'wrap',
    height: 'fit-content',
    marginTop: '20px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  makeOrderCreatedBlockLineOne: {
    display: 'flex',
    marginTop: '10px',
    borderBottom: '1px solid #c4bdbd',
    paddingBottom: '20px',
  },
  makeOrderCreatedBlockLineOneBasket: {
    textAlign: 'right'
  },
  makeOrderCreatedBlockLineOneTextPreview: {
    width: '77%'
  },
  makeOrderCreatedBlockLineOneTextMore: {
    textAlign: 'right',
    fontSize: '0.8rem',
    color: '#5D1049',
    cursor: 'pointer'
  },
  makeOrderCreatedBlockLineOneFirstRight: {
    paddingBottom: '5px',
    marginBottom: '10px',
    borderBottom: '1px solid #cec9c9'
  },
  basketIcon: {
    cursor: 'pointer'
  },
  makeOrderCreatedBlockLineOneProductNameLine: {
    fontSize: '1.5rem',
    fontWeight: '700'
  },
  makeOrderCreatedBlockLineOneProductNameLineInfo: {
    fontSize: '0.8rem',
  },
  makeOrderCreatedBlockLineOneSecondCircle: {
    backgroundColor: 'gold',
    width: '40px',
    height: '40px',
    borderRadius: '50%'
  },
  makeOrderCreatedBlockLineOneSecond: {
    display: 'flex'
  },
  makeOrderCreatedBlockLineOneSecondName: {
    flexBasis: '30%',
    textAlign: 'center'
  },
  makeOrderCreatedBlockLineOneSecondForm: {
    flexBasis: '30%',
    textAlign: 'center',
    display: 'flex',
    justifyContent: 'center'
  },
  makeOrderCreatedBlockLineOneSecondData: {
    flexBasis: '30%',
    textAlign: 'center'
  },
  makeOrderCreatedBlockLineOneSecondDataAll: {
    fontSize: '1.3rem',
    fontWeight: '700',
    color: '#5D1049'
  },
  makeOrderCreatedBlockLineOneSecondDataPerKg: {
    fontSize: '1.3rem',
    fontWeight: '700'
  },
  makeOrderCreatedBlockLineOneSecondDataRest: {
    textAlign: 'left',
    fontSize: '1rem',
    display: 'flex',
    justifyContent: 'space-between'
  },
  makeOrderCreatedBlockLineOneSecondDataIncome: {
    textAlign: 'left',
    fontSize: '1rem',
    display: 'flex',
    justifyContent: 'space-between'
  },
  makeOrderCreatedBlockLineOneSecondDataIncomeSup: {
    textAlign: 'right',
    fontSize: '0.7rem',
  },
  dataRestNumber: {
    color: '#FA1919'
  },
  dataNumber: {
    color: '#FA1919'
  },
  dataIncomeNumber: {
    color: '#5D1049'
  },
  makeOrderCreatedBlockLineTwo: {
    display:'flex',
    justifyContent: 'space-around',
    alignItems: 'center',
    marginTop: '10px',
    paddingBottom: '10px',
    borderBottom: '1px solid #a7a0a0',
  },
  countCheckbox: {
    fontSize: '3rem'
  },
  makeOrderCreatedBlockLineThree: {
    display: 'flex',
    paddingTop: '10px'
  },
  makeOrderCreatedBlockLineThreeLeft: {
    flexBasis: '40%'
  },
  makeOrderCreatedBlockLineThreeRight: {
    flexBasis: '40%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    textAlign: 'center'
  },
  makeOrderCreatedBlockLineThreeWeightCoverRight: {
    display: 'flex',
    justifyContent: 'space-evenly',
    alignItems: 'baseline',
    width: '100%',
    fontSize: '1.2rem'
  },
  makeOrderCreatedBlockLineThreeWeightBare: {
    textAlign: 'left',
    fontSize: '1rem',
    display: 'flex',
    justifyContent: 'space-between'
  },
  makeOrderCreatedBlockLineThreeWeightCloth: {
    textAlign: 'left',
    fontSize: '1rem',
    display: 'flex',
    justifyContent: 'space-between'
  },
  makeOrderCreatedBlockLineThreeWeightCover: {
    textAlign: 'left',
    fontSize: '1rem',
    display: 'flex',
    justifyContent: 'space-between'
  },
  dataNumberCoverRight: {
    fontSize: '1.5rem',
    fontWeight: '700',
    color: '#5D1049'
  },
  arrowBlock: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
}));

export default MakeOrderMakeOrderCreated;
